export default {
	data() {
		return {
			id:"",
			result:null
		}
	},
	onLoad(options) {
		this.id = options.id;
		this.getDetail(options.id);
	},
	methods: {
		getDetail(id){
			const that = this;
			let param = {
				id: id
			}
			that.$api.housepayDetail(param).then(res => {
				if (res.success) {
					let result = res.datas;
					result.memo = result.memo ?result.memo :"暂无";
					that.result = res.datas;
				} else {
					uni.showToast({
						title: res.message,
						icon: "none"
					})
				}
			})
		},
		gotoHouse(paimaiId,houseSource) {
			this.$Router.push({
				path: "/pages/home/goodsDetail",
				query:{
					paimaiId:paimaiId,
					houseSource:houseSource
				}
			})
		},
		gotoSelfPage(id){
			const that =this;
			that.$Router.push({
				path:"/pages/my/otherSelfPage",
				query:{
					id:id
				}
			})
		}
	}
}
